

















import { getBrandInfo } from '@/utils/apis'
import Vue from 'vue'
export default Vue.extend({
  name: 'Brand',
  data() {
    return {
     brandList: [],
      total: 0,
      queryParam: {
        pageNum: 1,
        pageSize: 20
      }
    }
  },
  mounted() {
    this.getBrandList()
  },
  methods: {
    async getBrandList() {
      const res = await getBrandInfo({})
      this.brandList = res.list
      this.total = res.total
    }
  }
})
